@font-face {
    font-family: 'PlayFairBold';
    src: url(../fonts/PlayfairDisplay-Bold.ttf);
}
@font-face {
    font-family: 'AssistantBold';
    src: url(../fonts/Assistant-Bold.ttf);
}
@font-face {
    font-family: 'AssistantRegular';
    src: url(../fonts/Assistant-Regular.ttf);
}
body {
    position: relative;
    background-color: #f2f2f2;
    margin: 0;
    scroll-behavior: smooth;
    min-height: 100vh;
    margin-top: 110px;
}


a {
    color: inherit;
}

h1 {
    color: #333333;
    font-family: 'PlayFairBold';
    text-align: center;
    font-size: 45px;
}

form {
    display: flex;
    flex-direction: row;
    height: 87px;
    width: 100%;
    z-index: 1;

    input[type=text]:not(.browser-default) {
        background-color: #fff;
        border: none;
        height: 100%;
        text-align: center;

        &::placeholder {
            color: #a8a8a8;
            font-family: 'AssistantBold';
            font-size: 26px;
            text-align: center
        }

        &:focus:not([readonly]) {
            border: 0;
            box-shadow: none;
        }
    }
    
    input[type=submit], button {
        background-color: #5833E8;
        color: white;
        font-family: 'AssistantBold';
        font-size: 26px;
        padding: 16px 30px;
        border: 0;
        height: 100%;
        min-width: 250px;
    }
}

.sidenav-trigger {
    display: none;
}


header {
    background-color: #fff;
    height: 110px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

    .links {
        a {
            font-size: 18px;
            font-family: 'AssistantRegular';
            margin: 0 35px;
        }
    }

    .title {
        img {
            width: 100px;
        }
        span {
            font-size: 50px;
            font-family: 'PlayFairBold';
            font-weight: bold;
        }
    }
}
#scrolled_header {
    display: none;
    justify-content: start;

    .links a {
        margin: 0 15px;
    }

    .title {
        margin-left: 40px;
        margin-right: 10px;
    }

    form {
        height: 60px;
        width: 500px;
        margin-left: auto;
        margin-right: 40px;

        input[type=text] {
            border: 1px solid #5833E8;

            &::placeholder {
                color: #a8a8a8;
                font-family: 'AssistantRegular';
                font-size: 20px;
                text-align: center
            }

        }
        button {
            height: 62px;
            min-width: 210px;
            font-size: 20px;
        }
    }
}

.send_req_block {
    background: url(../img/big_bckg.jpg) 0 0 no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 200px 0;
    position: relative;
    margin-bottom: 70px;

    .filter {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #000000 0% 0% no-repeat padding-box;
        opacity: 0.63;
        backdrop-filter: blur(8px);
        -webkit-backdrop-filter: blur(8px);
        z-index: 0;
    }

    .title {
        font-size: 100px;
        color: #FFFFFF;
        text-shadow: 0px 3px 6px #00000029;
        font-family: 'PlayFairBold';
        text-align: center;
        z-index: 1;
        margin: 0;
        line-height: 100px;
    }

    .sub_title {
        color: #FFFFFF;
        font-size: 37px;
        text-align: center;
        line-height: 37px;
        z-index: 1;
        font-family: 'AssistantBold';
    }
    form {
        width: 900px;
    }
}

.divider {
    height: 2px;
    width: 700px;
    background-color: #5833E8;
    margin: 30px auto;
    margin-bottom: 80px;
}

.cards {
    .card {
        margin: 10px 60px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        padding: 50px 10%;
        max-height: 500px;

        &:nth-child(2n) {
            flex-direction: row-reverse;

            .descr {
                margin-left: 0;
                margin-right: 200px;
            }
        }

        .descr {
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-left: 200px;

            .title {
                font-family: 'PlayFairBold';
                color: #333;
                font-size: 50px;
                line-height: 60px;
                margin-bottom: 40px;
            }
            .text {
                font-family: 'AssistantRegular';
                color: #000;
                line-height: 42px;
                font-size: 33px;
            }
        }

        img {
            width: 370px;
            object-fit: contain;
        }
    }
}

.send_req_block_bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 56px;

    h1 {
        
    }

    p {
        font-family: 'AssistantRegular';
        font-size: 33px;
        max-width: 900px;
        text-align: center;
    }
    form {
        max-width: 700px;
    }
}

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    padding: 50px 0;

    .logo {
        margin-bottom: 10px;

        img {
            width: 100px;
        }
        span {
            font-family: 'PlayFairBold';
            font-size: 50px;
            color: #000000;
        }
    }

    span {
        color: #5f5f5f;
        font-family: 'AssistantRegular';
        font-size: 20px;
    }
}

.modal {
    width: 720px;
    height: 400px;

    .modal-content {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: url(../img/this2.png) -100px 0 no-repeat;
        height: 100%;

        h1 {
            text-align: center;
            font-size: 33px;
            font-family: 'PlayFairBold';
            margin-bottom: 18px;
        }

        p {
            font-family: 'AssistantRegular';
            font-size: 20px;
            line-height: 26px;
            text-align: center;
            margin-bottom: 50px;
        }

        span {
            font-family: 'AssistantRegular';
            font-size: 15px;
            margin-bottom: 5px;
        }

        button {
            background-color: #5833E8;
            width: 228px;
            height: 55px;
            border: 1px solid #707070;
            color: #fff;
            font-family: 'AssistantBold';
            font-size: 18px;
            cursor: pointer;
        }

        .close_btn {
            position: absolute;
            width: 30px;
            height: 30px;
            color: #fff;
            background-color: #5833E8;
            top: 30px;
            right: 30px;
            text-align: center;
            font-size: 21px;
            font-family: 'AssistantBold';
            line-height: 27px;
            cursor: pointer;
        }
    }
}


.desk { display: inherit; }
.mob { display: none; }

@media only screen and (max-width: 450px) {
    form {
        height: 54px;

        input[type=text]:not(.browser-default) {

            &::placeholder {
                font-size: 16px;
            }
        }
        input[type=submit], button {
            font-size: 16px;
            padding: 16px 13px;
            min-width: 140px;
        }
    }
    .desk { display: none; }
    .mob { display: inherit; }

    .sidenav-trigger {
        display: block;
    }
    body {
        margin-top: 62px;
        background-color: #FFFFFF;
    }
    h1 {
        font-size: 30px;
        margin: 20px 0;
    }
    header {
        height: 62px;
        display: flex;
        justify-content: center;
        align-items: center;

        .links {
            display: none;
        }

        .menu {
            width: 34px;
            height: 24px;
            position: absolute;
            top: 18px;
            left: 20px;
        }

        .title {
            img {
                width: 54px;
                height: 26px;
            }
            span {
                font-size: 30px;
            }
        }
    }

    .send_req_block {
        width: 100%;
        background: url(../img/small_bckg.png) -235px 0 no-repeat;
        padding: 0 20px;
        padding-top: 45px;
        margin-bottom: 0;
        padding-bottom: 80px;

        .title {
            font-size: 54px;
            letter-spacing: 2.16px;
            line-height: 50px;
        }
        .sub_title {
            font-size: 22px;
            line-height: 25px;
            margin-bottom: 30px;
        }

        form {
            width: 100%;
        }
    }

    .cards {
        .card {
            box-shadow: none;
            padding: 30px 20px;
            margin: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            &:nth-child(2n+1) {
                background-color: #f1f1f1;
            }

            &:nth-child(2n) {
                flex-direction: column;
    
                .descr {
                    margin: 0;
                }
            }

            img {
                width: 220px;
                margin-bottom: 25px;
            }

            .descr {
                margin: 0;

                .title {
                    font-size: 28px;
                    text-align: center;
                    line-height: 34px;
                    margin-bottom: 20px;
                }
                .text {
                    font-size: 22px;
                    text-align: center;
                    line-height: 26px;
                }
            }
        }
    }

    .send_req_block_bottom {
        h1 {
            font-size: 37px;
        }

        p {
            font-size: 22px;
            text-align: center;
            margin: 0 40px;
        }

        form {
            padding: 5px 25px;
            height: 64px;
            margin: 40px 0;

            input[type=text]:not(.browser-default), input[type=submit], button {
                box-shadow: 0px 3px 6px #00000029;
                border: 1px solid #707070;
            }

            input[type=text] {
                background: #FFFFFF 0% 0% no-repeat padding-box;
                border-right-width: 0px;
            }
            input[type=submit], button {
                height: 56px;
                border-left-width: 0px;
            }
        }
    }

    footer {
        background-color: #f1f1f1;
        border-top: 1px solid #707070;
        padding: 15px;

        .logo {
            img {
                width: 53px;
            }
            span {
                font-size: 29px;
            }
        }

        span {
            font-size: 12px;
        }

    }

    .modal {
        width: 95%;
        height: 250px;

        .modal-content {
            background: none;

            h1 {
                font-size: 28px;
                margin-bottom: 0px;
            }
            p { margin-bottom: 20px; }
            p, span {
                font-size: 15px;
                line-height: 19px;
            }
            button {
                width: 150px;
                height: 40px;
                font-size: 16px;
            }
            .close_btn {
                width: 17px;
                height: 17px;
                top: 5px;
                right: 5px;
                font-size: 12px;
                line-height: 19px;
            }
        }
    }
}
